@import '../../variablesLight'

.App__task
  display: flex
  justify-content: space-between
  align-items: center
  font-size: 18px
  width: 100%
  padding: 12px 0
  border-radius: 5px
  color: $fontColor
  background: $lightBgColor
  font-weight: 400
  font-family: $mainFont
  transition: background .2s, color .2s, box-shadow .2s
  &:hover
    box-shadow: 0 0 20px 0 $shadowColor
    background: $bgColor
    color: $activeColor
    transition: background .2s, color .2s, box-shadow .2s


.App__editedTask
  display: flex
  justify-content: space-between
  align-items: center
  font-size: 18px
  width: 100%
  border-radius: 5px
  color: $darkColor
  background: $lightBgColor
  font-weight: 400
  font-family: $mainFont
  transition: background .2s, color .2s, box-shadow .2s


.App__task-title
  width: 80%
  overflow-wrap: anywhere
  padding-right: 15px
  flex: 1
